import React, { Component } from "react";
import {
  CBadge,
  CCard,
  CCardBody,
  CCardHeader,
  CCol,
  CRow,
  CSelect,
  CTooltip,
} from "@coreui/react";
import { Link } from "react-router-dom";
import api from "../../api";
import authHeader from "../../auth-header";
import { MDBDataTableV5 } from "mdbreact";
import Toast from "light-toast";
import swal from "sweetalert";
import { ScaleLoader } from "react-spinners";
import { css } from "@emotion/core";

const override = css`
  display: block;
  border-color: black;
  position: absolute;
  left: 43%;
  top: 50%;
  z-index: 1;
`;

class serviceProviderList extends Component {
  constructor(props) {
    super(props);
    this.fullname = this.fullname.bind(this);
    this.shopName = this.shopName.bind(this);
    this.serviceProviderStatus = this.serviceProviderStatus.bind(this);
    this.serviceProviderAction = this.serviceProviderAction.bind(this);
    this.serviceRemove = this.serviceRemove.bind(this);
    this.filter = this.filter.bind(this);

    this.state = {
      data: [],
      loading: false,
      businessTypeFilter: "",
      statusTypeFilter: "",
    };
  }

  fullname = (firstName, lastName) => {
    return firstName + " " + lastName;
  };

  shopName = (id, name) => {
    return (
      <>
        <CTooltip content="Click for more details" placement="top">
          <Link
            // className="btn submit-btn"
            style={{ color: "#1212bc" }}
            to={{
              pathname: "/service-provider-details",
              data: id, // your data array of objects
            }}
          >
            {name}
          </Link>
        </CTooltip>
      </>
    );
  };

  serviceProviderStatus = (id) => {
    if (id === 0) {
      return (
        <CBadge className="mr-1" color="danger">
          Inactive
        </CBadge>
      );
    } else if (id === 1) {
      return (
        <CBadge className="mr-1" color="success">
          Active
        </CBadge>
      );
    }
  };

  serviceProviderAction = (id, val) => {
    if (val === 1) {
      return (
        <div style={{ width: "94px" }}>
          <button
            className="btn cancel-btn"
            onClick={() => this.serviceRemove(id, val)}
          >
            <i className="fa fa-remove"></i>
          </button>
        </div>
      );
    } else {
      return (
        <div style={{ width: "94px" }}>
          <button
            className="btn cancel-btn"
            onClick={() => this.serviceRemove(id, val)}
          >
            <i className="fa fa-check"></i>
          </button>
        </div>
      );
    }
  };

  serviceRemove = (id, status) => {
    var data = {
      id: id,
      status: status,
    };

    // this.setState({ loading: true });
    swal({
      title: "Are you sure?",
      text: "Service Provider status will changed!",
      icon: "warning",
      buttons: true,
      dangerMode: true,
    }).then((willDelete) => {
      if (willDelete) {
        swal("Status updated successfully!", {
          icon: "success",
        });
        fetch(api.apiUrl + "delete_service_provider", {
          method: "POST",
          headers: authHeader(),
          body: JSON.stringify(data),
        })
          .then((response) => response.json())
          .then((responseJsonFromServerData) => {
            console.log(responseJsonFromServerData);
            if (responseJsonFromServerData === "success") {
              this.componentDidMount();
            } else {
              this.props.history.push("/404");
            }
          })
          .catch((error) => {
            console.error(error);
            this.props.history.push("/404");
          });
      } else {
        // swal("Your imaginary file is safe!");
      }
    });
  };

  componentDidMount() {
    var info = [];
    this.setState({ loading: true });
    fetch(api.apiUrl + "get_service_providers", {
      method: "POST",
      headers: authHeader(),
      // body: JSON.stringify(profile_data),
    })
      .then((response) => response.json())
      .then((responseJsonFromServerData) => {
        console.log(responseJsonFromServerData);
        if (responseJsonFromServerData.length !== 0) {
          responseJsonFromServerData.map((item, key) => {
            var arr = {
              name: this.fullname(item.firstName, item.lastName),
              shopName: this.shopName(item.id, item.shopName),
              contactNo: item.mobileNumber,
              businessType: item.businessType,
              status: this.serviceProviderStatus(item.status),
              action: this.serviceProviderAction(item.id, item.status),
            };
            info.push(arr);
          });
          this.setState({
            data: info,
            loading: false,
          });
        } else {
          //redirect to 404 page
          this.props.history.push("/404");
        }
      })
      .catch((error) => {
        console.error(error);
        this.setState({ loading: false });
      });
  }

  filter = (val, type) => {
    var info = [];

    // this.setState({
    //   serviceId: val,
    //   serviceType: type,
    // });

    if (type === "businessType") {
      this.setState({ businessTypeFilter: val });
      var data = {
        businessType: val,
        status:
          this.state.statusTypeFilter !== ""
            ? this.state.statusTypeFilter
            : "null",
      };
    }

    if (type === "status") {
      this.setState({ statusTypeFilter: val });
      var data = {
        businessType:
          this.state.businessTypeFilter !== ""
            ? this.state.businessTypeFilter
            : "null",
        status: val,
      };
    }
    this.setState({ loading: true });
    fetch(api.apiUrl + "fetch_service_providers", {
      method: "POST",
      headers: authHeader(),
      body: JSON.stringify(data),
    })
      .then((response) => response.json())
      .then((responseJsonFromServerData) => {
        console.log(responseJsonFromServerData);
        if (responseJsonFromServerData.length !== 0) {
          responseJsonFromServerData.map((item, key) => {
            var arr = {
              name: this.fullname(item.firstName, item.lastName),
              shopName: this.shopName(item.id, item.shopName),
              contactNo: item.mobileNumber,
              businessType: item.businessType,
              status: this.serviceProviderStatus(item.status),
              action: this.serviceProviderAction(item.id, item.status),
            };
            info.push(arr);
          });
          this.setState({
            data: info,
            loading: false,
          });
        } else {
          this.setState({ loading: false });
          Toast.info("No Data Found!!", 2000, () => {
            // do something after the toast disappears
          });
        }
      })
      .catch((error) => {
        console.error(error);
        this.setState({ loading: false });
        this.props.history.push("/404");
      });
  };
  render() {
    var datatable = {
      columns: [
        {
          label: "Name",
          field: "name",
          sort: "disabled",
          width: 150,
          attributes: {
            "aria-controls": "DataTable",
            "aria-label": "Icon",
          },
        },
        {
          label: "Shop Name",
          field: "shopName",
          sort: "disabled",
          width: 270,
        },
        {
          label: "Contact No.",
          field: "contactNo",
          sort: "disabled",
          width: 200,
        },
        {
          label: "Business Type",
          field: "businessType",
          sort: "disabled",
          width: 100,
        },
        {
          label: "Status",
          field: "status",
          sort: "disabled",
          width: 150,
        },
        {
          label: "Action",
          field: "action",
          sort: "disabled",
          width: 270,
        },
      ],
      rows: [...this.state.data],
    };

    return (
      <>
        <CRow>
          <CCol>
            <CCard>
              <ScaleLoader
                height={40}
                width={12}
                css={override}
                size={150}
                //size={"150px"} this also works
                color={"#B54545"}
                loading={this.state.loading}
              />
              <CCardHeader>
                <CRow>
                  <CCol sm="6" xs="6" md="8">
                    <h3>Service Providers</h3>
                  </CCol>
                  {/* <CCol sm="6" xs="6" md="4">
                    <CButton
                      className="submit-btn"
                      to="/add-service-provider"
                      style={{ float: "right" }}
                    >
                      Add
                    </CButton>
                  </CCol> */}
                </CRow>
              </CCardHeader>
              <CCardBody>
                <CRow>
                  <CCol md="3" sm="3" xs="12">
                    <CSelect
                      custom
                      name="ccmonth"
                      id="ccmonth"
                      onChange={(e) =>
                        this.filter(e.target.value, "businessType")
                      }
                      defaultValue={this.state.businessTypeFilter}
                      style={{ margin: "5px" }}
                    >
                      <option value="null">Select Business Type</option>
                      <option value="Sole Proprietorship">
                        Sole Proprietorship
                      </option>
                      <option value="Partnership">Partnership</option>
                      <option value="Corporation">Corporation</option>
                      <option value="Other">Other</option>
                    </CSelect>
                  </CCol>

                  <CCol md="3" sm="3" xs="12">
                    <CSelect
                      custom
                      name=""
                      id=""
                      onChange={(e) => this.filter(e.target.value, "status")}
                      defaultValue={this.state.statusTypeFilter}
                      style={{ margin: "5px" }}
                    >
                      <option value="null">Select Status</option>
                      <option value="1">Active</option>
                      <option value="0">Inactive</option>
                    </CSelect>
                  </CCol>
                </CRow>
                <CRow>
                  <CCol md="12" sm="12" xs="12">
                    <MDBDataTableV5
                      hover
                      entriesOptions={[6, 10]}
                      entries={6}
                      pagesAmount={4}
                      data={datatable}
                      pagingTop
                      searchTop
                      searchBottom={false}
                      barReverse
                      responsive
                    />
                  </CCol>
                </CRow>
              </CCardBody>
            </CCard>
          </CCol>
        </CRow>
      </>
    );
  }
}

export default serviceProviderList;
